import React, { Component, MouseEvent } from "react";
import { Button } from "react-bootstrap";
import "./Welcome.scss";
type myState = {};
type myProps = {};
export default class WelcomePage extends Component<myProps, myState> {
  constructor(props: myProps) {
    super(props);
    this._onClickButtonGG = this._onClickButtonGG.bind(this);
  }
  _onClickButtonGG(event: MouseEvent) {
    alert("hello click");
  }
  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col">
            <div className="welcome-header">Please sign in</div>
            <div className="page-header2">to discover more</div>
            <div className="main-login-block">
              <Button className="main-login-block__btn">By Email</Button>
              <Button className="main-login-block__btn">By SMS</Button>
            </div>
            <div> Or you can login by</div>
            <div className="social-login-block">
              <Button className="social-login-block__btn">Facebook</Button>
              <Button className="social-login-block__btn" onClick={this._onClickButtonGG}>
                Google
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
